<script>
import TableComponent from "@/components/Table/Table.vue";
import config from "../../../public/config/customSetting.json";

export default {
  name: "docSearchComponent",
  components: { TableComponent },
  data() {
    return {
      highRiskTypes: [],
      docSearchFacs: [],
      docSearchNonFabs: [],
      docSearchAreas: [],
      docSearchAllFunc: false,

      // 工程單申請
      projectApplyForm: {
        Create_Date1: "",
        Create_Date2: "",
        Project_Status: "ALL",
        IsPersonal: "1",
        Location_Type_Id: "2",
        Project_No: "",
        Location_Id: "LOCATION_ID",
        New_Id: "-999",
        Create_Date: [this.formatDate(new Date()), this.formatDate(new Date())],
      },
      projectApplyRule: {
        Project_Status: [
          { required: true, message: "必填", trigger: "change" },
        ],
        Create_Date: [{ required: true, message: "必填", trigger: "change" }],
      },
      projectApplyTableColumns: [
        {
          id: "index",
          label: "項次",
          width: "70",
        },
        {
          id: "project_No",
          label: "申請單號",
          width: "140",
        },
        {
          id: "project_Name",
          label: "工程名稱",
          width: "140",
        },
        {
          id: "createDate",
          label: "建立日期(開始日期~結束日期)",
          width: "250",
        },
        {
          id: "workSpace",
          label: "工作區域",
          width: "200",
        },
        {
          id: "contract",
          label: "主承包商編號 主承包商名稱/下承包商數",
          width: "300",
        },
        {
          id: "status",
          label: "簽核節點",
          width: "150",
        },
      ],
      projectApplyFilterLocation: [],
      projectTableData: [],
      projectTableList: [],
      projectApplyPage: 1,

      // 每日施工單
      dailyWorkForm: {
        Work_Date1: "",
        Work_Date2: "",
        Project_No: "",
        Apply_Pk: "",
        Daily_Status: "ALL",
        Location_Id: "LOCATION_ID",
        daily_Status: "ALL",
        New_Id: "-999",
        Cancel_Status: "ALL",
        Location_Type_Id: "2",
        work_Date: [new Date(), new Date()],
        Work_Type_Pk: "ALL",
      },
      dailyWorkRule: {
        Daily_Status: [{ required: true, message: "必填", trigger: "change" }],
        work_Date: [{ required: true, message: "必填", trigger: "change" }],
      },
      dailyWorkTableColumns: [
        {
          id: "index",
          label: "項次",
          width: "70",
        },
        {
          id: "apply_Pk",
          label: "施工單編號",
          width: "150",
        },
        {
          id: "project_No",
          label: "工程單編號",
          width: "150",
        },
        {
          id: "detailArea",
          label: "詳述區域/施工廠商名稱",
          width: "200",
        },
        {
          id: "work_Content",
          label: "施工內容",
          width: "200",
        },
        {
          id: "wtn",
          label: "施工種類",
          width: "200",
        },
        {
          id: "eng_Name",
          label: "負責工程師",
          width: "150",
        },
        {
          id: "status",
          label: "簽核",
          width: "120",
        },
        {
          id: "principal",
          label: "廠商施工負責人/工安/人數",
          width: "150",
        },
      ],
      dailyWorkFilterLocation: [],
      dailyWorkEnterWriteOffSelected: [],
      dailyWorkRetiredWriteOffSelected: [],
      dailyWorkTableData: [],
      dailyWorkTableList: [],
      dailyWorkPage: 1,

      // 緊急搶修單
      emergencyRepairForm: {
        Begin_Date: [this.formatDate(new Date()), this.formatDate(new Date())],
        Begin_Date1: "",
        Begin_Date2: "",
        IsPersonal: "0",
        Project_No: "",
        Urgent_Status: "ALL",
        Location_Type_Id: "2",
        Location_Id: "LOCATION_ID",
        New_Id: "",
        Cancel_Status: "ALL",
      },
      emergencyRepairRule: {
        Urgent_Status: [{ required: true, message: "必填", trigger: "change" }],
        Begin_Date: [{ required: true, message: "必填", trigger: "change" }],
      },
      emergencyRepairTableColumns: [
        {
          id: "index",
          label: "項次",
          width: "70",
        },
        {
          id: "name",
          label: "緊急搶修單編號/緊急搶修名稱",
          width: "300",
        },
        {
          id: "compilation",
          label: "廠商名稱/統編",
          width: "170",
        },
        {
          id: "date",
          label: "建立日期(開始日期~結束日期)",
          width: "250",
        },
        {
          id: "workSpace",
          label: "工作區域",
          width: "200",
        },
        {
          id: "status",
          label: "緊急搶修單狀態",
          width: "140",
        },
        {
          id: "available",
          label: "可用狀態",
          width: "140",
        },
      ],
      emergencyRepairFilterLocation: [],
      emergencyRepairEnterWriteOffSelected: [],
      emergencyRepairRetiredWriteOffSelected: [],
      emergencyRepairTableData: [],
      emergencyRepairTableList: [],
      emergencyRepairPage: 1,
    };
  },
  computed: {},
  methods: {
    // 共用
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    // 匯出檔案處理
    exportExcelHandler(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          if (formName === "projectApplyForm") {
            this.projectApplyExport(this.projectApplyForm);
          } else if (formName === "dailyWorkForm") {
            this.dailyWorkExport(this.dailyWorkForm);
          } else {
            this.emergencyRepairExport(this.emergencyRepairForm);
          }
        } else {
          this.$message({
            type: "error",
            duration: "5000",
            showClose: true,
            message: "資料填寫錯誤",
          });
        }
      });
    },
    // 下載檔案
    exportExcel(data, fileName) {
      const href = URL.createObjectURL(data);
      const link = document.createElement("a");
      link.href = href;
      link.setAttribute("download", `${fileName}.xlsx`);
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      URL.revokeObjectURL(href);
    },
    // API取得資料
    async getPrjCondition() {
      try {
        const res = await this.$store.dispatch("docSearchQryPrjCondition");
        const data = res.data.data;
        this.highRiskTypes = data.highRiskTypes;
        this.docSearchFacs = JSON.parse(JSON.stringify(data.facs));
        this.docSearchNonFabs = JSON.parse(JSON.stringify(data.nonFabs));
        this.docSearchAreas = JSON.parse(JSON.stringify(data.areas));
        this.docSearchAllFunc = JSON.parse(JSON.stringify(data.allFunc));
      } catch (err) {
        console.log(err);
      }
    },

    // 工程單申請
    async getProjectList(formData) {
      try {
        const params = this.getURLNewParams(formData);
        const res = await this.$store.dispatch(
          "docSearchQryProjectList",
          params
        );
        let data = res.data.data;
        this.projectTableData = JSON.parse(JSON.stringify(data));
        this.projectTableData.forEach((e, index) => {
          e.index = index + 1;
          e.createDate = `${e.create_Date}(${e.begin_Date}~${e.end_Date})`;
          e.workSpace = `${e.ld2}/${e.ld4}/${e.floor2}`;
          e.contract = `${e.s_Id}${e.s_Name}/${e.countVendor}`;
          e.status = this.convertStatus(e.project_Status);
        });
        this.projectTableList = this.projectTableData.slice(0, 10);
        this.projectApplyPage = 1;
      } catch (err) {
        console.log(err);
      }
    },
    projectApplyFacsHandler(val) {
      let data = JSON.parse(JSON.stringify(this.docSearchAreas));
      this.projectApplyFilterLocation = data.filter(
        (e) => e.location_Id === val
      );
      this.projectApplyForm.New_Id = "";
    },
    searchProjectApplyForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (this.projectApplyForm.Location_Id === "LOCATION_ID") {
            this.$message({
              type: "error",
              duration: "5000",
              showClose: true,
              message: "未填入申請單號時，請選取廠區！",
            });
          } else {
            this.projectApplyForm.Create_Date1 =
              this.projectApplyForm.Create_Date[0];
            this.projectApplyForm.Create_Date2 =
              this.projectApplyForm.Create_Date[1];
            this.getProjectList(this.projectApplyForm);
          }
        } else {
          this.$message({
            type: "error",
            duration: "5000",
            showClose: true,
            message: "資料填寫錯誤",
          });
        }
      });
    },
    async projectApplyViewSignOffDetails(prjNo) {
      try {
        const getProjDetailRes = await this.$store.dispatch(
          "getProjDetail",
          prjNo
        );
        const getSignOffHistoryRes = await this.$store.dispatch(
          "getSignOffHistory",
          prjNo
        );
        let data = [getProjDetailRes.data.data, getSignOffHistoryRes.data.data];
        const { href } = this.$router.resolve({
          path: "/viewSignOffDetails",
          query: {
            data: JSON.stringify(data),
          },
        });
        window.open(href, "_blank");
      } catch (err) {
        console.log(err);
      }
    },
    projectApplyChangePage(page) {
      this.projectApplyPage = page;
      const cur = (page - 1) * 10;
      const last = page * 10;
      this.projectTableList = this.projectTableData.slice(cur, last);
    },
    async projectApplyExport(form) {
      if (!form.Project_No && !form.Location_Id) {
        this.$message({
          type: "error",
          duration: "5000",
          showClose: true,
          message: "未填入申請單號時，請選取廠區！",
        });
      } else {
        try {
          form.Create_Date1 = form.Create_Date[0];
          form.Create_Date2 = form.Create_Date[1];
          const res = await this.$store.dispatch("commonExportExcel", {
            obj: form,
            router_Name: config.getDocSearch,
            action_Name: "ExportProject",
          });
          this.exportExcel(res.data, "工程申請單");
        } catch (err) {
          console.log(err);
        }
      }
    },

    // 每日施工單
    async getDailyList(formData) {
      try {
        const params = this.getURLNewParams(formData);
        const res = await this.$store.dispatch("docSearchQryDailyList", params);
        let data = res.data.data;
        this.dailyWorkTableData = JSON.parse(JSON.stringify(data));
        this.dailyWorkTableData.forEach((e, index) => {
          e.index = index + 1;
          e.detailArea = `${e.location_Name}${e.location_Type_Description}${e.floor}(${e.area_Name})/${e.supplier_Short_Name}`;
          e.eng_Name = `${e.eng_Name}(ext=${e.eng_Ext_No})`;
          e.status = this.convertStatus(e.status);
          e.wtn = `${e.wtn ? e.wtn : "一般作業"}`;
          e.principal = `施: ${e.duty_Name}(${e.duty_Tel || "未填"}) 工: ${
            e.safe_Name
          }/ 共${e.member_Count}人`;
        });
        this.dailyWorkTableList = this.dailyWorkTableData.slice(0, 10);
      } catch (err) {
        console.log(err);
      }
    },
    searchDailyWorkForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          if (
            !this.dailyWorkForm.Project_No &&
            !this.dailyWorkForm.Apply_Pk &&
            this.dailyWorkForm.Location_Id === "LOCATION_ID"
          ) {
            this.$message({
              type: "error",
              duration: "5000",
              showClose: true,
              message: "工程單號或施工單號或廠區選擇必選其一",
            });
          } else {
            this.dailyWorkForm.Work_Date1 = this.formatDate(
              this.dailyWorkForm.work_Date[0]
            );
            this.dailyWorkForm.Work_Date2 = this.formatDate(
              this.dailyWorkForm.work_Date[1]
            );
            this.getDailyList(this.dailyWorkForm);
            this.dailyWorkPage = 1;
          }
        } else {
          this.$message({
            type: "error",
            duration: "5000",
            showClose: true,
            message: "資料填寫錯誤",
          });
        }
      });
    },
    dailyWorkFacsHandler(val) {
      let data = JSON.parse(JSON.stringify(this.docSearchAreas));
      this.dailyWorkFilterLocation = data.filter((e) => e.location_Id === val);
      this.dailyWorkForm.New_Id = "";
    },
    dailyWorkChangePage(page) {
      this.dailyWorkPage = page;
      const cur = (page - 1) * 10;
      const last = page * 10;
      this.dailyWorkTableList = this.dailyWorkTableData.slice(cur, last);
    },
    async dailyWorkExport(form) {
      if (
        !form.Project_No &&
        !form.Apply_Pk &&
        form.Location_Id === "LOCATION_ID"
      ) {
        this.$message({
          type: "error",
          duration: "5000",
          showClose: true,
          message: "工程單號或施工單號或廠區選擇必選其一！",
        });
      } else {
        try {
          form.Work_Date1 = this.formatDate(form.work_Date[0]);
          form.Work_Date2 = this.formatDate(form.work_Date[1]);
          const res = await this.$store.dispatch("commonExportExcel", {
            obj: form,
            router_Name: config.getDocSearch,
            action_Name: "ExportDaily",
          });
          this.exportExcel(res.data, "每日施工單");
        } catch (err) {
          console.log(err);
        }
      }
    },
    dailyWorkEnterWriteOff(evt, item) {
      if (evt) {
        this.dailyWorkEnterWriteOffSelected.push(item);
        item.EnterChecked = true;
      } else {
        this.dailyWorkEnterWriteOffSelected =
          this.dailyWorkEnterWriteOffSelected.filter(
            (e) => e.index !== item.index
          );
        item.EnterChecked = false;
      }
    },
    dailyWorkRetiredWriteOff(evt, item) {
      if (evt) {
        this.dailyWorkRetiredWriteOffSelected.push(item);
        item.RetiredChecked = true;
      } else {
        this.dailyWorkRetiredWriteOffSelected =
          this.dailyWorkRetiredWriteOffSelected.filter(
            (e) => e.index !== item.index
          );
        item.RetiredChecked = false;
      }
    },
    async dailyWorkEnterWriteOffConfirm() {
      try {
        const arr = this.dailyWorkEnterWriteOffSelected.map((e) => e.apply_Pk);
        const obj = {
          Enter: arr,
          Retired: [],
        };
        const res = await this.$store.dispatch("docSearchUpdCancelData", obj);
        if (res.data.isOK) {
          this.$message({
            type: "success",
            duration: 5000,
            showClose: true,
            message: "到場核銷成功",
          });
          this.$refs.dailyWorkTable.$refs.multipleTable.clearSelection();
          this.getDailyList(this.dailyWorkForm);
          this.dailyWorkPage = 1;
          this.dailyWorkEnterWriteOffSelected = [];
          this.dailyWorkRetiredWriteOffSelected = [];
        }
      } catch (err) {
        console.log(err);
      }
    },
    async dailyWorkRetiredWriteOffConfirm() {
      try {
        const arr = this.dailyWorkRetiredWriteOffSelected.map(
          (e) => e.apply_Pk
        );
        const obj = {
          Enter: [],
          Retired: arr,
        };
        const res = await this.$store.dispatch("docSearchUpdCancelData", obj);
        if (res.data.isOK) {
          this.$message({
            type: "success",
            duration: 5000,
            showClose: true,
            message: "離場核銷成功",
          });
          this.$refs.dailyWorkTable.$refs.multipleTable.clearSelection();
          this.getDailyList(this.dailyWorkForm);
          this.dailyWorkPage = 1;
          this.dailyWorkEnterWriteOffSelected = [];
          this.dailyWorkRetiredWriteOffSelected = [];
        }
      } catch (err) {
        console.log(err);
      }
    },
    viewHandler(item) {
      const { href } = this.$router.resolve({
        path: "/engineeringDataManagementView",
        query: {
          data: JSON.stringify(item),
        },
      });
      window.open(href, "_blank");
    },
    viewDetailHandler(apply_Pk) {
      window.open(
        `/#/engineeringDataManagementViewDetail/${encodeURIComponent(apply_Pk)}`
      );
    },
    printHandler(apply_Pk) {
      window.open(
        `/#/engineeringDataManagementPrint/${encodeURIComponent(apply_Pk)}`
      );
    },

    // 緊急搶修單
    async getUrgentList(formData) {
      try {
        const params = this.getURLNewParams(formData);
        const res = await this.$store.dispatch(
          "docSearchQryUrgentList",
          params
        );
        let data = res.data.data;
        this.emergencyRepairTableData = JSON.parse(JSON.stringify(data));
        this.emergencyRepairTableData.forEach((e, index) => {
          e.index = index + 1;
          e.name = `${e.project_No}/${e.project_Name}`;
          e.compilation = `${e.s_Name}/${e.s_Id}`;
          e.date = `${e.create_Date}(${e.begin_Date}~${e.end_Date})`;
          e.workSpace = `${e.ld2}(${e.ld1})${e.ld4}(${e.ld3})${e.floor}`;
          e.status = this.convertStatus(e.project_Status);
          e.available = e.deleted === "0" ? "有效" : "已刪除";
        });
        this.emergencyRepairTableList = this.emergencyRepairTableData.slice(
          0,
          10
        );
      } catch (err) {
        console.log(err);
      }
    },
    emergencyRepairFacsHandler(val) {
      let data = JSON.parse(JSON.stringify(this.docSearchAreas));
      this.emergencyRepairFilterLocation = data.filter(
        (e) => e.location_Id === val
      );
      this.dailyWorkForm.New_Id = "";
    },
    searchEmergencyRepairForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          this.emergencyRepairForm.Begin_Date1 =
            this.emergencyRepairForm.Begin_Date[0];
          this.emergencyRepairForm.Begin_Date2 =
            this.emergencyRepairForm.Begin_Date[1];
          this.getUrgentList(this.emergencyRepairForm);
          this.emergencyRepairPage = 1;
        } else {
          this.$message({
            type: "error",
            duration: "5000",
            showClose: true,
            message: "資料填寫錯誤",
          });
        }
      });
    },
    emergencyRepairChangePage(page) {
      this.emergencyRepairPage = page;
      const cur = (page - 1) * 10;
      const last = page * 10;
      this.emergencyRepairTableList = this.emergencyRepairTableData.slice(
        cur,
        last
      );
    },
    emergencyRepairEnterWriteOff(evt, item) {
      if (evt) {
        this.emergencyRepairEnterWriteOffSelected.push(item);
        item.EnterChecked = true;
      } else {
        this.emergencyRepairEnterWriteOffSelected =
          this.emergencyRepairEnterWriteOffSelected.filter(
            (e) => e.index !== item.index
          );
        item.EnterChecked = false;
      }
    },
    emergencyRepairRetiredWriteOff(evt, item) {
      if (evt) {
        this.emergencyRepairRetiredWriteOffSelected.push(item);
        item.RetiredChecked = true;
      } else {
        this.emergencyRepairRetiredWriteOffSelected =
          this.emergencyRepairRetiredWriteOffSelected.filter(
            (e) => e.index !== item.index
          );
        item.RetiredChecked = false;
      }
    },
    async emergencyRepairEnterWriteOffConfirm() {
      try {
        const arr = this.emergencyRepairEnterWriteOffSelected.map(
          (e) => e.project_No
        );
        const obj = {
          Enter: arr,
          Retired: [],
        };
        const res = await this.$store.dispatch(
          "docSearchUpdUrgentCancelData",
          obj
        );
        if (res.data.isOK) {
          this.$message({
            type: "success",
            duration: 5000,
            showClose: true,
            message: "到場核銷成功",
          });
          this.$refs.emergencyRepairTable.$refs.multipleTable.clearSelection();
          this.getUrgentList(this.emergencyRepairForm);
          this.emergencyRepairPage = 1;
          this.emergencyRepairRetiredWriteOffSelected = [];
          this.emergencyRepairEnterWriteOffSelected = [];
        }
      } catch (err) {
        console.log(err);
      }
    },
    async emergencyRepairRetiredWriteOffConfirm() {
      try {
        const arr = this.emergencyRepairRetiredWriteOffSelected.map(
          (e) => e.project_No
        );
        const obj = {
          Enter: [],
          Retired: arr,
        };
        const res = await this.$store.dispatch(
          "docSearchUpdUrgentCancelData",
          obj
        );
        if (res.data.isOK) {
          this.$message({
            type: "success",
            duration: 5000,
            showClose: true,
            message: "離場核銷成功",
          });
          this.$refs.emergencyRepairTable.$refs.multipleTable.clearSelection();
          this.getUrgentList(this.emergencyRepairForm);
          this.emergencyRepairPage = 1;
          this.emergencyRepairRetiredWriteOffSelected = [];
          this.emergencyRepairEnterWriteOffSelected = [];
        }
      } catch (err) {
        console.log(err);
      }
    },
    async emergencyRepairExport(form) {
      try {
        form.Begin_Date1 = form.Begin_Date[0];
        form.Begin_Date2 = form.Begin_Date[1];
        const res = await this.$store.dispatch("commonExportExcel", {
          obj: form,
          router_Name: config.getDocSearch,
          action_Name: "ExportUrgent",
        });
        this.exportExcel(res.data, "緊急搶修單");
      } catch (err) {
        console.log(err);
      }
    },
    async openDetails(prjNo) {
      try {
        const res = await this.$store.dispatch("getViewSignOffDetail", prjNo);
        if (res.data.isOK) {
          const data = res.data.data;
          const { href } = this.$router.resolve({
            path: "/emergencyRepairOrderViewSighOffDetails",
            query: {
              data: JSON.stringify(data),
            },
          });
          window.open(href, "_blank");
        }
      } catch (err) {
        console.log(err);
      }
    },
    async getPrintFormInfo(prjNo) {
      try {
        //const res = await this.$store.dispatch("getPrintFormInfo", prjNo);
        //if (res.data.isOK) {
        //const data = res.data.data;
        const { href } = this.$router.resolve({
          path: "/emergencyRepairOrderViewSignOffDetails",
          query: {
            data: prjNo, //JSON.stringify(data),
          },
        });
        window.open(href, "_blank");
        //}
      } catch (err) {
        console.log(err);
      }
    },
  },
  async mounted() {
    this.getPrjCondition();
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;
  },
};
</script>

<template src="./docSearch.html"></template>
